/*----------------
Global
----------------*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}



/*----------------
Body
----------------*/ 

.container {
    max-width: 700px;
    margin: 0 auto;
}

.card {
    width: 100%;   
}
.box {
    width: 100%;
    margin-bottom: 2rem;
    border: 1px solid black;
    padding: 1rem;
}

.box:hover{
    box-shadow: 5px;
}

.hide {
    display:none;
}
.order-form{
    width: 100%;
}

.form-item, .btn{ 
    height: 4rem;
    width: 100%;
    margin-bottom: 1rem;

}

.btn{
    font-size: 1.5rem;
    color: #EEE;
    border: 0;
    background: rgb(51,128,129);
    background: linear-gradient(0deg, rgba(51,128,129,1) 0%, rgba(62,172,173,1) 100%);
   
}

.site-header{
    background-color: #234a5b;
    color: white;
    display: -webkit-flex;
    display: flex;
    height: 4rem;
    margin-bottom: 3rem;
    padding: 0 0.6rem;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
}
.logo{
    /* justify-content: space-between;
    /* width: 30%; */
   float : top;
    float:left;
    display: -webkit-flex;
    display: flex;
    
    padding-top:12px;
    margin-left:-10px;
    -webkit-align-items: center;
            align-items: center;
    

   
}


.nav-list{
    display: -webkit-flex;
    display: flex;
    list-style: none;
}

.nav-list-item{
    margin: 0.7rem;
}
.nav-list-item:last-child{
    margin-right: 0;
}
.wrapper{
    display : grid;
    grid-column: auto;
}
